import { WatchlistDataTablePercentageCell } from "../../WatchlistDataTableCells/WatchlistDataTablePercentageCell"
import { genericColumn } from "../../genericColumn"

function ebtPerEbitTtm(tableName: string) {
  const title = "EBT per EBIT (LTM)"
  return genericColumn({
    endpoint: "companyRatios",
    columnKeys: ["ebtPerEbitTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Share of operating income left after interest.",
    cell: info => {
      return (
        <WatchlistDataTablePercentageCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyRatios}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about EBT per EBIT or other profitability measures?`,
              value: info.row.original.financialData.companyRatios?.data?.ebtPerEbitTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={(info.row.original.financialData.companyRatios?.data?.ebtPerEbitTtm ?? 0) * 100}
        />
      )
    },
  })
}

export { ebtPerEbitTtm }
